        <div class="burger_icon" v-for=" (item, index) in this.$store.state.foodList" :key="item.name">
            <span :price="item.price"><button @click="delFood(index)">྾</button> <div v-text="item.name"></div><i v-if="item.quantity>1">x {{item.quantity}}</i></span>
            <img :src="item.img | imgUrl" alt="">
