        </div>
    </div>
</template>
<script>
import Loading from '@/components/Loading'
export default {
  components: {
    Loading
  },
  data () {
    return {
      animationName: ''
    }
  },
  watch: {
    '$store.state.foodList.length': function (val) {
      this.animationName = ''
      setInterval(() => {
        this.animationName = 'shakeBurger'
      }, 100)
    }
  },
  methods: {
    getImgUrl (imageName) {
      return require(`../assets/images/${imageName}.svg`) // the module request
    },
    delFood (index) {
      this.$store.state.foodList.splice(index, 1)
    }
  }
}
