<template>
    <div class="order">
        <h2>訂單管理</h2>
        <table>
            <tbody>
                <tr v-for="order in this.pageShow" :key="order._id">
                    <td>
                        <div>{{order._id | idFormat}}</div>
                        <span>{{order.date | dateFormat}}</span>
                    </td>
                    <td>
                        <h4>${{order.totalPrice}}</h4>
                        <h6>($20+${{order.totalPrice - 20}})</h6>
                    </td>
                    <td>
                        <span class="label" v-for="(food, key, index) in order.foodList" :key="index">
                            {{key}} x{{food}}
                        </span>
                    </td>
                </tr>
            </tbody>
        </table>
        <v-pagination v-if="pagesLength > 1"
          v-model="page"
          :length="pagesLength"
      ></v-pagination>
    </div>
</template>
<script>
export default {
  data () {
    return {
      orders: [],
      page: 1
    }
  },
  computed: {
    pagesLength () {
      return Math.floor((this.orders.length - 1) / 5) + 1
    },
    pageShow () {
      return this.orders.slice((this.page - 1) * 5, (this.page - 1) * 5 + 5)
    }
  },
  created () {
    this.getOrderData()
  },
  watch: {
    '$store.state.foodList.length': function (val) {
      if (val === 0) {
        this.getOrderData()
      }
    }
  },
  methods: {
    getOrderData () {
      this.$http.get('orders')
        .then((response) => {
          this.orders = response.data

          for (let index = 0; index < response.data.length; index++) {
            const foodList = response.data[index].foodList
            const obj = { }
            for (let index = 0; index < foodList.length; index++) {
              if (obj[foodList[index]]) {
                obj[foodList[index]]++
              } else {
                obj[foodList[index]] = 1
              }
            }
            this.orders[index].foodList = obj
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>
