<template>
    <div class="price">
        <div>
          <span>Total:</span>
          <h3>${{totalPrice + 20}} <small>($20 + ${{totalPrice}})</small></h3>
        </div>
        <button @click="sendOrder(totalPrice),
                        editStock()"
                :disabled="this.$store.state.foodList.length==0">
                送出</button>
    </div>
</template>
<script>
export default {
  computed: {
    totalPrice: function () {
      let totalPrice = 0
      this.$store.state.foodList.forEach(product => {
        totalPrice += (product.price * product.quantity)
      })
      return totalPrice
    }
  },
  methods: {
    sendOrder (totalPrice) {
      const arr = []
      for (let index = 0; index < this.$store.state.foodList.length; index++) {
        for (let quantity = 0; quantity < this.$store.state.foodList[index].quantity; quantity++) {
          arr.push(this.$store.state.foodList[index].name)
        }
      }
      this.$http.post('orders', {
        foodList: arr,
        totalPrice: totalPrice + 20
      })
        .then((response) => {
          this.$store.state.foodList = []
        })
        .catch((error) => {
          console.log(error)
        })
    },
    editStock () {
      const foodList = this.$store.state.foodList
      for (let index = 0; index < foodList.length; index++) {
        this.$http.put('products', {
          name: foodList[index].name,
          stock: -(foodList[index].quantity)
        })
          .then((response) => {
            this.$store.state.products.find(o => o.name === foodList[index].name).stock = response.data.stock
          })
          .catch((error) => {
            console.log(error)
          })
      }
    }
  }
}
</script>
