<template>
    <div class="stock_view">
        <h2>庫存管理</h2>
        <table class="table">
            <thead>
                <tr>
                  <th>品項</th>
                  <th>售價</th>
                  <th>庫存</th>
                </tr>
                <tr>
                  <th>品項</th>
                  <th>售價</th>
                  <th>庫存</th>
                </tr>
            </thead>
            <tbody >
                <tr v-for="item in this.$store.state.products" :key="item.name">
                    <td>{{item.name}}</td>
                    <td>{{item.price}}</td>
                    <td>{{item.stock}}
                      <div class="button_group">
                        <button @click="editStock(item.name, 1)"  :disabled="item.stock>=9">＋</button>
                        <button @click="editStock(item.name, -1)" :disabled="item.stock<=0">－</button>
                      </div>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
  created () {
    this.getData()
  },
  methods: {
    getData () {
      this.$http.get('products')
        .then((response) => {
          this.$store.state.products = response.data
        })
        .catch((error) => {
          this.setTestFildError(false, error)
        })
    },
    editStock (name, val) {
      this.$http.put('products', {
        name: name,
        stock: val
      })
        .then((response) => {
          this.$store.state.products.find(o => o.name === name).stock = response.data.stock
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>

