<template>
    <div class="menu">
      <button v-for="product in this.$store.state.products"
              :key="product.name"
              :price="product.price"
              @click=" add(product.name, product.price, product.img)"
              :disabled="clickTime[product.name] >= product.stock">
        <i><img :src=" product.img | imgUrl"></i>
        <span>{{product.name}}</span>|{{product.price}}
      </button>
    </div>
</template>
<script>
export default {
  data () {
    return {
      clickTime: {
        肉排: 0,
        培根: 0,
        火腿: 0,
        起司: 0,
        小黃瓜: 0,
        蛋: 0,
        生菜: 0,
        洋蔥: 0,
        番茄: 0,
        番茄醬: 0,
        美乃滋: 0,
        芥末: 0
      }
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    add (name, price, img) {
      if (!this.$store.state.foodList.some(el => el.name === name)) {
        this.$store.state.foodList.push(
          {
            name: name,
            price: price,
            img: img,
            quantity: 1
          }
        )
      } else {
        this.$store.state.foodList.find(o => o.name === name).quantity += 1
      }
      this.clickTime[name]++
    },
    getData () {
      this.$http.get('products')
        .then((response) => {
          this.$store.state.products = response.data
          this.$store.state.loading = false
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>
