import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import moment from 'moment-timezone'
import vuetify from './plugins/vuetify'

Vue.config.productionTip = false

Vue.prototype.$http = axios
axios.defaults.baseURL = 'https://biggerking-api.onrender.com/api/'

window.onresize = function () { location.reload() }

Vue.filter('dateFormat', function (value) {
  return moment.tz(value, 'Asia/Taipei').format('YYYY/MM/DD HH:mm:ss')
})

Vue.filter('idFormat', function (value) {
  const pattern = /.{8}$/ //eslint-disable-line
  return value.match(pattern)[0].toUpperCase()
})
new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
